<template>
  <view class="container">
    <view class="header">
      <text class="title">ÂÆåÂñÑ‰∏™‰∫∫‰ø°ÊÅØ</text>
      <text class="subtitle">ËØ∑ËÆæÁΩÆ‰Ω†ÁöÑÁî®Êà∑ÂêçÂπ∂ÈÄâÊã©‰∏Ä‰∏™ÂñúÊ¨¢ÁöÑÂ§¥ÂÉè</text>
    </view>
    
    <!-- Áî®Êà∑ÂêçËÆæÁΩÆ -->
    <view class="username-section" v-if="!loading">
      <view class="input-group">
        <text class="section-title">Áî®Êà∑Âêç</text>
        <input 
          class="username-input" 
          type="text" 
          placeholder="ËØ∑ËÆæÁΩÆÊÇ®ÁöÑÁî®Êà∑ÂêçÂπ∂ÈÄâÊã©‰∏Ä‰∏™ÂñúÊ¨¢ÁöÑÂ§¥ÂÉè" 
          v-model="username"
          @input="onUsernameInput"
          maxlength="10"
          confirm-type="done"
        />
      </view>
      <view class="username-tips">
        <text class="tips-text">{{usernameLength}}/10</text>
      </view>
    </view>
    
    <!-- Âä†ËΩΩ‰∏≠ -->
    <view class="loading" v-if="loading">
      <view class="loading-spinner"></view>
      <text>Ê≠£Âú®Âä†ËΩΩÂ§¥ÂÉè...</text>
    </view>
    
    <!-- Â§¥ÂÉèÈÄâÊã©Ê†áÈ¢ò -->
    <view class="avatar-header" v-if="!loading">
      <text class="section-title">ÈÄâÊã©Â§¥ÂÉè</text>
    </view>
    
    <!-- Â§¥ÂÉèÂàóË°® -->
    <view class="avatar-grid" v-if="!loading">
      <view 
        :class="['avatar-item', selectedAvatar === avatar ? 'selected' : '']" 
        v-for="(avatar, index) in avatars" 
        :key="index"
        @tap="selectAvatar(avatar)"
      >
        <image class="avatar-image" :src="avatar" mode="aspectFill"></image>
        <text class="avatar-name">Â§¥ÂÉè{{index + 1}}</text>
      </view>
    </view>
    
    <!-- ÊåâÈíÆÂå∫Âüü -->
    <view class="buttons" v-if="!loading">
      <button class="btn btn-default" @tap="useDefaultAvatar" :disabled="!isUsernameValid">‰ΩøÁî®ÈªòËÆ§Â§¥ÂÉè</button>
      <button class="btn btn-primary" @tap="confirmSelection" :disabled="!selectedAvatar || !isUsernameValid">Á°ÆËÆ§ÈÄâÊã©</button>
    </view>
    
    <!-- ÊèêÁ§∫‰ø°ÊÅØ -->
    <view class="tips-section" v-if="!loading">
      <text class="tips-info" v-if="!isUsernameValid">‚ö†Ô∏è ËØ∑ËæìÂÖ•2-10‰∏™Â≠óÁ¨¶ÁöÑÁî®Êà∑Âêç</text>
      <text class="tips-info" v-else-if="!selectedAvatar">üí° ËØ∑ÈÄâÊã©‰∏Ä‰∏™Â§¥ÂÉè</text>
      <text class="tips-success" v-else>‚úÖ ‰ø°ÊÅØÂÆåÂñÑÔºåÂèØ‰ª•ÂºÄÂßãÊ∏∏Êàè‰∫ÜÔºÅ</text>
    </view>
  </view>
</template>

<script>
export default {
  data() {
    return {
      loading: true,
      username: '',
      selectedAvatar: '',
      avatars: [
        '/assets/avatars/avatar1.svg',
        '/assets/avatars/avatar2.svg',
        '/assets/avatars/avatar3.svg',
        '/assets/avatars/avatar4.svg',
        '/assets/avatars/avatar5.svg',
        '/assets/avatars/avatar6.svg'
      ]
    }
  },
  computed: {
    usernameLength() {
      return this.username.length
    },
    isUsernameValid() {
      return this.username.length >= 2 && this.username.length <= 10
    }
  },
  onLoad() {
    this.initData()
  },
  methods: {
    async initData() {
      try {
        // Âä†ËΩΩÁî®Êà∑‰ø°ÊÅØ
        const userInfo = uni.getStorageSync('userInfo') || {}
        this.username = userInfo.username || ''
        this.selectedAvatar = userInfo.avatarUrl || ''
      } catch (error) {
        console.error('ÂàùÂßãÂåñÊï∞ÊçÆÂ§±Ë¥•:', error)
      } finally {
        this.loading = false
      }
    },
    onUsernameInput(e) {
      this.username = e.detail.value
    },
    selectAvatar(avatar) {
      this.selectedAvatar = avatar
    },
    useDefaultAvatar() {
      if (!this.isUsernameValid) return
      this.selectedAvatar = '/assets/avatars/avatar1.svg'
      this.confirmSelection()
    },
    async confirmSelection() {
      if (!this.selectedAvatar || !this.isUsernameValid) return
      
      try {
        uni.showLoading({
          title: '‰øùÂ≠ò‰∏≠...'
        })
        
        // Êõ¥Êñ∞Áî®Êà∑‰ø°ÊÅØ
        const userInfo = {
          ...uni.getStorageSync('userInfo'),
          username: this.username,
          avatarUrl: this.selectedAvatar
        }
        
        // TODO: Ë∞ÉÁî®ÂêéÁ´ØÊé•Âè£Êõ¥Êñ∞Áî®Êà∑‰ø°ÊÅØ
        
        uni.setStorageSync('userInfo', userInfo)
        
        uni.showToast({
          title: '‰øùÂ≠òÊàêÂäü',
          icon: 'success'
        })
        
        setTimeout(() => {
          uni.navigateBack()
        }, 1500)
      } catch (error) {
        console.error('‰øùÂ≠òÁî®Êà∑‰ø°ÊÅØÂ§±Ë¥•:', error)
        uni.showToast({
          title: '‰øùÂ≠òÂ§±Ë¥•',
          icon: 'none'
        })
      } finally {
        uni.hideLoading()
      }
    }
  }
}
</script>

<style>
.container {
  min-height: 100vh;
  padding: 40rpx;
  background-color: #fff;
}

.header {
  text-align: center;
  margin-bottom: 60rpx;
}

.title {
  font-size: 36rpx;
  font-weight: bold;
  color: #333;
  display: block;
  margin-bottom: 10rpx;
}

.subtitle {
  font-size: 28rpx;
  color: #666;
  display: block;
}

.username-section {
  margin-bottom: 40rpx;
}

.input-group {
  margin-bottom: 10rpx;
}

.section-title {
  font-size: 28rpx;
  color: #333;
  margin-bottom: 20rpx;
  display: block;
}

.username-input {
  width: 100%;
  height: 88rpx;
  background-color: #f5f5f5;
  border-radius: 44rpx;
  padding: 0 30rpx;
  font-size: 28rpx;
}

.username-tips {
  text-align: right;
}

.tips-text {
  font-size: 24rpx;
  color: #999;
}

.loading {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 60rpx 0;
}

.loading-spinner {
  width: 60rpx;
  height: 60rpx;
  border: 6rpx solid #f3f3f3;
  border-top: 6rpx solid #4CAF50;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 20rpx;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.avatar-header {
  margin-bottom: 30rpx;
}

.avatar-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 30rpx;
  margin-bottom: 60rpx;
}

.avatar-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20rpx;
  border-radius: 16rpx;
  background-color: #f5f5f5;
  transition: all 0.3s ease;
}

.avatar-item.selected {
  background-color: #E8F5E9;
  border: 2rpx solid #4CAF50;
}

.avatar-image {
  width: 120rpx;
  height: 120rpx;
  border-radius: 60rpx;
  margin-bottom: 10rpx;
}

.avatar-name {
  font-size: 24rpx;
  color: #666;
}

.buttons {
  display: flex;
  gap: 20rpx;
  margin-bottom: 40rpx;
}

.btn {
  flex: 1;
  height: 88rpx;
  border-radius: 44rpx;
  font-size: 28rpx;
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
}

.btn::after {
  display: none;
}

.btn-default {
  background-color: #f5f5f5;
  color: #333;
}

.btn-primary {
  background-color: #4CAF50;
  color: #fff;
}

.btn[disabled] {
  background-color: #BDBDBD;
  color: #fff;
}

.tips-section {
  text-align: center;
}

.tips-info {
  font-size: 26rpx;
  color: #FF9800;
}

.tips-success {
  font-size: 26rpx;
  color: #4CAF50;
}
</style> 